<template>
  <div id="app">
    <el-card class="table-card">
      <div slot="header">
        原料信息
      </div>
      <el-form>
        <el-form-item label="搜索">
          <el-col :span="18">
            <el-input v-model="searchContent" placeholder="搜索原料名称或编号"></el-input>
          </el-col>
          <el-button type="primary" plain @click="fuzzySearch()">搜索</el-button>
        </el-form-item>
      </el-form>
      <el-table :data="searchVisible?temp_list:items" height="250" stripe border>
        <el-table-column prop="mId" label="编号"></el-table-column>
        <el-table-column prop="mName" label="名称"></el-table-column>
        <el-table-column prop="unit" label="单位"></el-table-column>
        <el-table-column label="编辑">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="editMaterial(scope.row, scope.$index)">编辑</el-button>
          </template>
        </el-table-column>
        <el-table-column label="删除">
          <template slot-scope="scope">
            <el-button type="danger" size="mini" @click="delMaterial(scope.row, scope.$index)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-button type="primary" plain @click="getItems()">试一试</el-button>
    <el-button type="primary" plain @click="addMaterial()">添加</el-button>
    <el-button type="primary" plain @click="back_home">返回</el-button>
    <el-dialog title="提示" :visible="delVisible">
      <span>Sure to delete?</span>
      <span slot="footer">
        <el-button type="danger" @click="handleDel()">确认删除</el-button>
        <el-button type="primary" @click="delVisible = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog :title="addFlag?'新增原料':'修改原料'" :visible="addVisible">
      <el-form>
        <el-form-item label="编号">
          <el-input v-model="cur_item.mId" placeholder="Please input id"></el-input>
        </el-form-item>
        <el-form-item label="名称">
          <el-input v-model="cur_item.mName" placeholder="Please input name"></el-input>
        </el-form-item>
        <el-form-item label="单位">
          <el-input v-model="cur_item.unit" placeholder="Please input unit"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="success" @click="saveMaterial()">提交</el-button>
        <el-button type="primary" @click="resetMaterial()">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
      temp_list: [],
      cur_item: {},
      orig_item: {},
      curIndex: 1,
      delVisible: false,
      addVisible: false,
      searchVisible: false,
      searchContent: '',
      addFlag: true
    }
  },
  ready: function() {
    this.$http.get('http://127.0.0.1:8000/backend/unk/', function(data) {
      this.$set('items', data);
    })
  },
  methods: {
    back_home() {
      location.assign('../index.html')
    },
    getItems() {
      this.$http.get('http://127.0.0.1:8000/backend/unk/').then(
        function(data) {
          this.$set('items', data)
        }
      ),
      this.$notify({
        title: 'Success',
        message: 'See you!',
        duration: 6000
      })
    },
    editMaterial(row, curIndex) {
      this.addVisible = true,
      this.addFlag = false,
      this.cur_item = row,
      this.orig_item = row,
      this.curIndex = curIndex
    },
    delMaterial(row, curIndex) {
      this.delVisible = true,
      this.cur_item = row,
      this.curIndex = curIndex
    },
    handleDel() {
      this.items.splice(this.curIndex, 1)
      this.delVisible = false
    },
    saveMaterial() {
      this.items.splice(this.curIndex, 1, this.cur_item),
      this.addVisible = false
    },
    resetMaterial() {
      this.addVisible = false,
      this.items.splice(this.curIndex, 1, this.orig_item)
    },
    addMaterial() {
      this.addVisible = true,
      this.cur_item = {},
      this.orig_item = {},
      this.items.push(this.cur_item),
      this.curIndex = this.items.length - 1
    },
    fuzzySearch() {
      this.temp_list = this.items.slice(0)
      this.searchVisible = true
      for(var i = 0; i < this.temp_list.length; i++) {
        if(this.temp_list[i].name.indexOf(this.searchContent) == -1 && this.temp_list[i].id.indexOf(this.searchContent) == -1) {
          this.temp_list.splice(i, 1),
          i--
        }
      }
    }
  }
}
</script>

<style>
.table-card {
  width: 800px;
  margin: 100px auto;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}
</style>
